import React, { Fragment, useState } from "react";
import {
  GlobalStyle,
  Gnb,
  Article,
  Header,
  Main,
  Explane,
} from "../style/style";
import Navigation from "../content/Navigation";
import Weather from "../content/Weather";

function Skill() {
  const [isHover, setIsHover] = useState(false);
  const [expSkill, setExpSkill] = useState();
  const skills = [
    {
      id: 0,
      skill: "HTML",
      icon: "/img/icons/iconHtml",
      text: "# # # #HTML5 # # #",
    },
    {
      id: 1,
      skill: "CSS",
      icon: "/img/icons/iconCss",
      text: "# # #UI # #",
    },
    {
      id: 2,
      skill: "JAVASCRIPT",
      icon: "/img/icons/iconJs",
      text: "# # # #DOM # #AJAX",
    },
    {
      id: 3,
      skill: "JQUERY",
      icon: "/img/icons/iconJq",
      text: "#DOM # # # #UI",
    },
    {
      id: 4,
      skill: "BOOTSTRAP",
      icon: "/img/icons/iconBoot",
      text: "# # #UI #",
    },
    {
      id: 5,
      skill: "REACT",
      icon: "/img/icons/iconReact",
      text: "# # #UI #SPA #ReactHooks",
    },
    {
      id: 6,
      skill: "AXIOS",
      icon: "/img/icons/iconAxios",
      text: "#HTTP #AJAX # #API #",
    },
    {
      id: 7,
      skill: "STYLED-COMPONENTS",
      icon: "/img/icons/iconSC",
      text: "#CSS-in-JS # #",
    },
    {
      id: 8,
      skill: "FIREBASE",
      icon: "/img/icons/iconFirebase",
      text: "# #Firestore #Auth #storage",
    },
    {
      id: 9,
      skill: "FIGMA",
      icon: "/img/icons/iconFIgma",
      text: "#UI # # #",
    },
    {
      id: 10,
      skill: "GITHUB",
      icon: "/img/icons/iconGithub",
      text: "# # # #",
    },
  ];
  const hoverEvt = (id) => {
    const explaneSkill = skills.filter((item) => item.id == id);
    setExpSkill(explaneSkill);
    setIsHover(true);
  };
  return (
    <GlobalStyle>
      <Gnb className="menu">
        <Navigation />
      </Gnb>
      <Header className="header">
        <h2>SKILL</h2>
      </Header>
      <Article className="article">
        <Weather />
      </Article>
      <Main className="main" $padding="100px 0">
        <div className="skillBox">
          {skills.map((item) => (
            <img
              key={item.id}
              src={`${item.icon}.png`}
              alt={item.skill}
              onMouseOver={() => hoverEvt(item.id)}
              onMouseOut={() => setIsHover(false)}
            />
          ))}
        </div>
      </Main>
      <Explane className="explane">
        <div className="exSkill">
          {isHover ? (
            expSkill.map((item) => (
              <Fragment key={item.id}>
                <img src={`${item.icon}c.png`} alt={item.skill} />
                <p>{item.text}</p>
              </Fragment>
            ))
          ) : (
            <>
              <span
                style={{
                  display: " block",
                  width: "100px",
                  height: "100px",
                  margin: " 0",
                }}
              ></span>
              <p>  !</p>
            </>
          )}
        </div>
      </Explane>
    </GlobalStyle>
  );
}

export default Skill;
